<template>
	<div
		class="progressbar"
		:style="{width: progress}">
	</div>
</template>

<script>
	
	export default {
		name: 'ProgressBar',
		props: {
			pageHeight: {
				type: Number,
				required: true
			},
			scrollHeight: {
				type: Number,
				required: true
			}
		},
		data() {
			return {
				calcPageHeight: 0
			}
		},
		created() {
			this.calcPageHeight = this.pageHeight - document.body.clientHeight
		},
		computed: {
			progress() {
				return (Math.round(this.scrollHeight / this.calcPageHeight * 100) + '%')
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.progressbar
		position fixed
		z-index 9999
		top 0
		left 0
		width 0%
		height 4px
		transition width .8s linear
		background linear-gradient(to right, var(--dark), var(--secondary), var(--primary))

</style>
