<template>
	<div class="article">
		<progress-bar
			v-if="isShowProgress"
			:pageHeight="$store.state.pageHeight"
			:scrollHeight="$store.state.scrollY" />
			
		<div class="article-wrap" v-if="isComplete">
			<article-cover :info="info" class="article-cover" />
			
			<div class="article-container">
				<div id="article" class="article-content"
					v-html="articleContent"
					v-highlight
					ref="content">
				</div>
				<div class="article-sidebar">
					<side-bar></side-bar>
					<toc ref="toc"></toc>
				</div>
			</div>
		</div>
		
		<floating-button
			v-if="isShowProgress"
			:pageHeight="$store.state.pageHeight"
			:scrollHeight="$store.state.scrollY" />
			
	</div>
</template>

<script>
	
	import ProgressBar from '@/components/ProgressBar/ProgressBar'
	import ArticleCover from '@/components/ArticleCover/ArticleCover'
	import SideBar from '@/components/SideBar/SideBar'
	import Toc from '@/components/Toc/Toc'
	import FloatingButton from '@/components/FloatingButton/FloatingButton'
	
	import { UPDATE_PAGE_HEIGHT } from '@/store/mutation-types'

	export default {
		name: 'BlogArticle',
		data() {
			return {
				articleContent: '',
				info: {},
				isComplete: false,
				isShowProgress: false,
				pageHeight: 0
			}
		},
		components: {
			ProgressBar,
			ArticleCover,
			SideBar,
			Toc,
			FloatingButton
		},
		created() {
			const article = this.$store.getters.article(this.$route.query.title)

			if (undefined === article)
				this.$get('/article', { title: this.$route.query.title }).then(this._initData)
			else
				this._initData(article)
		},
		methods: {
			_initData(article) {
				
				this.info = article.data
				// this.articleContent = this.rmd.render(article.content)
				this.articleContent = article.content
				this.isComplete = true
				
				this.$nextTick(() => {
					this.$refs.toc.initToc(this.$refs.content)
					// 等待数据加载完毕再显示 progressbar
					this._initProgressData()
				})
			},
			_initProgressData() {
				this.$store.commit(UPDATE_PAGE_HEIGHT)
				this.isShowProgress = true
			}
		}
	}

</script>

<style lang="stylus" scoped>
	
	.article
		position absolutes
		width 100%
		background-color #00030B
		scroll-behavior smooth
		
		.article-cover
			width 100%
			height 42rem
			
		.article-wrap
			width 100%
			height 100%
			background-color #fff
		
			.article-container
				width 120rem
				height 100%
				margin 0 auto
				padding 4rem 0
				display grid
				grid-template-columns minmax(0, 1fr) 30rem
				grid-gap 4rem
				
			@media screen and (max-width: 400px)
				.article-container
					width 90%
					overflow hidden
					display block
					
					.article-sidebar
						display none

</style>
