<template>
	<ul class="sidebar-info">
		<li class="sidebar-info-item">
			<router-link to="/">
				<span>{{ articleNum }}</span>
				<p>文章</p>
			</router-link>
		</li>
		
		<li class="sidebar-info-item">
			<router-link to="/category">
				<span>{{ cateroryNum }}</span>
				<p>分类</p>
			</router-link>
		</li>
		
		<li class="sidebar-info-item">
			<router-link to="/tag">
				<span>{{ tagNum }}</span>
				<p>标签</p>
			</router-link>
		</li>
	</ul>
</template>

<script>
	
	import { INFO } from '@/store/mutation-types'
	
	export default {
		name: 'SideBarInfo',
		created() {
			this.$get('/info').then(data => this.$store.commit(INFO, data))
		},
		computed: {
			articleNum() {
				return this.$store.state.info.articles.length
			},
			cateroryNum() {
				return Object.keys(this.$store.state.info.categories).length
			},
			tagNum() {
				return Object.keys(this.$store.state.info.tags).length
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.sidebar-info
		display flex
		margin-top 2rem
		
		.sidebar-info-item
			flex 1
			text-align center
			
			span
				color #0396FF
			
		.sidebar-info-item:nth-child(2)
			border-left 1px solid rgba(158,158,158,.16)
			border-right 1px solid rgba(158,158,158,.16)

	
</style>
