<template>
	<div id="toc">
		<div class="toc-subbox">
			<h4 class="toc-title">目录</h4>
			<ol class="toc">
				<li
					class="toc-item"
					v-for="(item, i) in list"
					:class="{active: tocActiveId === i}"
					:key="i">
					<a
						class="toc-link"
						:href="item.id">
						<span class="toc-number">{{ i + 1 }}. </span>
						<span class="toc-content">{{ item.name }}</span>
					</a>
					<ol
						class="toc-subtoc"
						v-if="item.children.length > 0">
						<li
							class="toc-subtoc-item"
							v-for="(subitem, j) in item.children"
							:class="{subactive: isActive(subitem.pos)}"
							:key="j">
							<a
								class="toc-subtoc-link"
								:href="subitem.id">
								<span class="toc-number">{{ i + 1 }}.{{ j + 1 }}. </span>
								<span class="toc-content">{{ subitem.name }}</span>
							</a>
						</li>
					</ol>
				</li>
			</ol>
		</div>
	</div>
</template>

<script>
	
	export default {
		name: 'Toc',
		data() {
			return {
				list: [],
				tocActiveId: -1,
				halfScreenHeight: 0
			}
		},
		created() {
			this.halfScreenHeight = document.body.clientHeight / 2
		},
		methods: {
			initToc(content) {
				content.querySelectorAll('h1').forEach(e => e.classList.add('md-toc'))
				content.querySelectorAll('h2').forEach(e => e.classList.add('md-toc'))
				
				const eles = content.querySelectorAll('.md-toc')
				let level = 0

				// 收集 h1 和 h2 形成数据对象
				eles.forEach(el => {
					
					level ++
					
					const id = 'toc-level-' + level,
						name = el.innerText,
						pos = el.offsetTop
						
					el.id = id
					el.classList.remove('md-toc')
					
					if (el.tagName === 'H1') {
						this.list.push({
							name,
							pos,
							id: '#' + id,
							children: []
						})
					}
					else {
						this.list[this.list.length - 1].children.push({
							name,
							pos,
							id: '#' + id
						})
					}
				})
			}
		},
		watch: {
			'$store.state.scrollY'(newValue) {
				const op = newValue + this.halfScreenHeight
				this.list.forEach((value, index) => {
					if ((op > value.pos) && (this.tocActiveId !== index))
						this.tocActiveId = index
				})
			}
		},
		computed: {
			isActive() {
				return pos => {
					return (this.$store.state.scrollY + this.halfScreenHeight) > pos
				}
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	@keyframes cir
		100%
			transform translateY(-50%) scale(1.6)
			opacity 0
	
	#toc
		padding 1.4rem 0
		width 30rem
		background-color #f5f5f5
		border-radius 2rem
		transform translateX(1.4rem)
		margin-top 6rem
		position -webkit-sticky
		position sticky
		top 8rem
		
		.toc-subbox
			width 100%
			padding 0 2rem
			background-color #fff
			border-radius 2rem
			box-shadow 0 0 20px rgba(0, 0, 0, .2)
			transform translateX(-1.4rem)
			overflow hidden
			
			.toc-title
				margin 2rem 0

			.toc
				margin 2rem 0 2rem 3px
				border-left 1px solid #ccc
				position relative
				padding-left 1.4rem
				
				& > .toc-item
					height 2.6rem					overflow hidden

				.toc-item
					position relative
					width 100%
					height 2.6rem
					font-size 1.4rem
					line-height 2.6rem
					font-size 1.5rem
					color #9e9e9e
					overflow hidden

				.toc-number
					margin-right 4px
			
				& > .active
					height auto
					overflow visible
					
					.toc-link
						color #0396ff
						position relative
					
						&::before
							content ''
							position absolute
							left -1.9rem
							top 50%
							transform translateY(-50%)
							width 1rem
							height 1rem
							border-radius 50%
							background-color deepskyblue
							
						&::after
							content ''
							position absolute
							left -2.1rem
							top 50%
							transform translateY(-50%) scale(.7)
							width 1.4rem
							height 1.4rem
							box-sizing border-box
							border-radius 50%
							opacity .8
							border 1px solid deepskyblue
							animation cir 1s infinite
						
			.toc-subtoc
				padding-left 1.4rem
				
				.toc-subtoc-item
					width 100%
					font-size 1.4rem
					position relative
					line-height 2.6rem
					font-size 1.5rem
					color #9e9e9e
				
					.toc-subtoc-link
						display block
						width 100%
						overflow hidden
						white-space nowrap
						text-overflow ellipsis
						
				& > .subactive
					.toc-subtoc-link
						color transparent
						background: linear-gradient(25deg, #0052d4, #0066ff)
						background-clip text

</style>
